  import MovingObject from './moving_object';
  import Skier from './skier';
  import Tree from './tree';


  Game.NUM_TREES = 50;

  class Game {
    constructor(canvas) {
      this.ctx = canvas.getContext("2d");
      this.dimX = 800;
      this.dimY = 900;

      this.gameOver = false;
      this.playing = true;
      this.collision = false;

      this.trees = [];
      this.setGame();

      this.play = this.play.bind(this);

      key('space', () =>  {
        if(this.playing === true){
          this.playing = false;
        } else if (this.playing === false) {
          this.playing = true;
          this.play();
        }
      } );
      key('left', () =>  {
        if(this.playing === true){
          this.trees.forEach(tree => {
            tree.moveLeft(this.ctx);
          });
          this.checkCollisions();
        }
      } );
      key('down', () =>  {
        if(this.playing === true){
          this.trees.forEach(tree => {
            tree.moveDown(this.ctx);
          });
          this.checkCollisions();
        }
      } );
      key('right', () =>  {
        if(this.playing === true){
          this.trees.forEach(tree => {
            tree.moveRight(this.ctx);
          });
          this.checkCollisions();
        }
      });
    }


    setGame() {
      this.skier = new Skier;
      this.skier.draw(this.ctx);
      this.addTree();
      this.trees.forEach (tree => {
        tree.draw(this.ctx);
      });
      this.play();
    }

    resetGame() {
      this.skier.clear(this.ctx);
      this.skier.reset();
      this.trees.forEach(tree => {
        tree.clear(this.ctx);
        tree.reset();
      });
      this.trees = [];
    }


    addTree() {
      for (let i = 0; i < this.NUM_TREES; i++) {
        this.trees.push(new Tree);
      }
    }


    checkCollisions(){
      this.trees.find( tree =>{
        if(this.skier.collision(tree) && this.collision === false){
          this.collision = true;
          this.skier.lives -= 1;
        }
      });
    }

    wrap(object){
      if((object.direction === "south" && object.pos.y > this.dimY + 100)||
      (object.direction === "north" && object.pos.y < -this.dimX - 100)){
        return object.resetPosition();
      }
    }



  play(){
    if (this.playing === false){
      return;
    }
    if (this.collision && this.skier.lives === 0){
      alert("GAME OVER");
      this.playing = false;
      this.gameover = true;
      this.resetGame();
    } else if (this.collision && this.skier.lives > 0){
      console.log(this.skier.lives);
      this.skier.clear(this.ctx);
      this.skier.reset();
      this.skier.move();
      this.skier.draw(this.ctx);
    }
    this.trees.forEach( tree => {
      tree.clear(this.ctx);
      tree.move();
      this.wrap(tree);
      tree.draw(this.ctx);
      this.collision = false;
    });
    window.requestAnimationFrame(this.play);
  }
}








export default Game;

